import { useEffect, useState } from "react";
import { getPositions, getToken, postUser } from "usersAPI/usersAPI";
import { PostFormik } from "../PostFormik/PostFormik";

export const PostRequest = () => {
   const [positions, setPositions] = useState([]);
   const [status, setStatus] = useState('idle');
   const [err, setErr] = useState(null);
   const [saveToken, setSaveToken] = useState(null)
   const [formData, setFormData] = useState(null)

   console.log(saveToken);

   const data = (value) => {
      setFormData(value);
   };

   useEffect(() => {
      async function searchPositionsAndToken() {
         setStatus('pending');
         try {
            const response = await getPositions();
            const { token } = await getToken();
            setPositions(response.positions)
            setSaveToken(token);
            setStatus('resolve');
         } catch (err) {
            setErr('Error! Sorry...')
            setStatus('rejected');
         };
      };

      searchPositionsAndToken();
   }, [])

   useEffect(() => {

      if (!formData) {
         return
      };

      async function postRequestUser(formData, saveToken) {
         setStatus('pending');
         try {
            const response = await postUser(formData, saveToken);
            setPositions(response.positions)
            setStatus('resolve');
         } catch (err) {
            setErr('Error! Sorry...')
            setStatus('rejected');
         };
      };

      postRequestUser(formData, saveToken);
   }, [formData, saveToken])


   if (status === 'pending') {
      return <div className="post-request-pending">Loading...</div>
   }

   if (status === 'rejected') {
      return <div className="post-request-rejected">{err}</div>
   }

   if (status === 'resolve') {
      return (
         <section className="post-request">
            <div className="post-request__container">
               <div className="post-request__body">
                  <h2 className="post-request__title title">Working with POST request</h2>
                  <PostFormik positions={positions} data={data} />
               </div>
            </div>
         </section>
      )
   };
};